<template>
<div>
  <!--    面包屑导航-->
  <el-breadcrumb style="padding-bottom:15px " separator="/">
    <el-breadcrumb-item :to="{ path: '/main' }">首页</el-breadcrumb-item>
    <el-breadcrumb-item>文章管理</el-breadcrumb-item>
    <el-breadcrumb-item>已审核</el-breadcrumb-item>
  </el-breadcrumb>
  <el-card>
    <!--    搜索头部-->
    <el-row :gutter="40" style="padding-bottom: 15px">
      <el-col :span="8">
        <el-input placeholder="请输入内容" v-model="input3" class="input-with-select" @keyup.enter.native="Search"
                  clearable>
          <el-button slot="append" icon="el-icon-search" @click="Search"></el-button>
        </el-input>
      </el-col>
    </el-row>
    <el-table
        :data="EssayList.data"
        border
        style="width: 100%">
      <el-table-column
          prop="title"
          label="标题"
          width="300">
      </el-table-column>
      <el-table-column
          prop="concent"
          label="内容"
          width="300">
      </el-table-column>
      <el-table-column
          prop="addtime"
          label="添加时间"
          width="250">
      </el-table-column>
      <el-table-column
          prop="autor"
          label="作者"
          width="200">
      </el-table-column>
      <el-table-column
          prop="id"
          label="操作"
          width="200">
        <div slot-scope="a">
          <el-button type="primary" size="mini" @click="updateEssay(a.row)">查看/编辑</el-button>
          <el-button type="danger" size="mini" @click="deleteEssay(a.row)">删除</el-button>
        </div>
      </el-table-column>
    </el-table>
  </el-card>
  <!--      分页控件-->
  <el-pagination
      @size-change="SizeChange"
      @current-change="CurrentChange"
      :current-page="page"
      :page-sizes="[10, 20, 50, 100]"
      :page-size="limit"
      layout="total, sizes, prev, pager, next, jumper"
      :total="EssayList.count">
  </el-pagination>
  <el-dialog
      title="编辑文章"
      :visible.sync="dialogUpdateEssay"
      width="30%">
    <!--        内容区域-->
    <el-form :model="UpdateEssayList"  ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <el-form-item label="标题" prop="title">
        <el-input v-model="UpdateEssayList.title" ></el-input>
      </el-form-item>
      <el-form-item label="内容" prop="concent">
        <el-input  type="textarea" :rows="2" v-model="UpdateEssayList.concent" ></el-input>
      </el-form-item>
      <el-form-item label="作者" prop="autor">
        <el-input   :rows="2" v-model="UpdateEssayList.autor " disabled></el-input>
      </el-form-item>
      <el-form-item label="添加时间" prop="addtime">
        <el-input   :rows="2" v-model="UpdateEssayList.addtime " disabled></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
    <el-button @click="dialogUpdateEssay = false">取 消</el-button>
    <el-button type="primary" @click="updateEssay1">保 存</el-button>
  </span>
  </el-dialog>
</div>
</template>

<script>
export default {
  name: "VerifyEssay",
  data(){
    return{
      input3:'',
      page: 1,
      limit: 10,
      statecode: '1',
      EssayList:[],
      UpdateEssayList:{
        id:'',
        title:'',
        concent:'',
      },
      dialogUpdateEssay: false,
    }
  },
  methods:{
    //请求查看用户列表数据
    getEssayList() {
      this.$http.get("/EssayConroller/selectVerifyEssay", {
        params: {
          statecode:this.statecode
        }
      }).then((res) => {
        // console.log(res)
        this.EssayList = res;
        // if (res.data.code === 1) {
        //   // this.$message.success("请求成功")
        // } else this.$message.error("请求失败")
      })
    },
    //编辑按钮事件
    updateEssay(row) {
      this.dialogUpdateEssay = !this.dialogUpdateEssay
      this.UpdateEssayList = row;
      console.log(this.UpdateEssayList)
    },
    //确定编辑事件
    updateEssay1() {
      this.dialogUpdateEssay = !this.dialogUpdateEssay,
          this.$http.get("/EssayConroller/updateEssay", {
            params: {
              id: this.UpdateEssayList.id,
              title: this.UpdateEssayList.title,
              concent: this.UpdateEssayList.concent,
            }
          }).then((res) => {
            console.log(res)
            if (res.data.code === 1) {
              this.$message.success("保存成功")
              this.getEssayList();
            } else this.$message.error("保存失败")
          })
    },
    //删除按钮事件
    deleteEssay(row) {
      this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$http.get("/EssayConroller/deleteEssay", {
          params: {
            id: row.id,
          }
        }).then((res) => {
          console.log(res)
          if (res.data.code === 1) {
            this.$message.success("删除成功")
            this.getEssayList();
          } else this.$message.error("删除失败")
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    //每页条数发生改变时触发
    SizeChange(limit) {
      this.limit = limit;
      this.getEssayList();
    },
    //页码发生改变时触发
    CurrentChange(page) {
      this.page = page;
      this.getEssayList();
    },
  },
  //  钩子函数
  created() {
    this.getEssayList();
  },
}

</script>

<style scoped>

</style>